*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
   
  }

.heading{
  
  animation-name: start;
  animation-duration: 3s;
  animation-iteration-count: 1;
  display: flex;
  
  justify-content: center;
  margin-top: 50px;
  animation-timing-function: linear;
 
  
}
.heading2{
  animation-name: start;
  animation-duration: 3s;
  animation-iteration-count: 1;
  display: flex;
  
  justify-content: center;
  animation-timing-function: linear;
}
.p1___{
  font-size: 40px;
  
}
.p2____{
  font-size: 40px;
  
  
}
@keyframes start{
  from{
    transform: translateX(-50%);
    opacity:0;
  }
  to{
    transform: translateX(0%);
    opacity:1;
  }
}
.Square{
  margin-top:1cm;
  width:100%;
  height: 0.1cm;
  background-color: slategrey;
  animation-name: square;
  animation-duration: 3s;
  animation-iteration-count: 1;
}
@keyframes square{
  from{
    opacity:0;
  }
  to{
    opacity:1;
  }
}
.content{
  
  margin-top: 100px;
  
  padding-left: 20%;
  padding-right: 20%;

}
.contents{
  display: flex;
  justify-content: center;
  animation-name: text;
  animation-duration: 2s;
  animation-iteration-count: 1;

}
@keyframes text{
  from{
    transform: translateY(-100%);
    border-color: white solid;
  }
  to{
    transform: translateY(0);
    border-color: cornflowerblue solid;
  }
}